.faixa-header {
    background-color: #124e70;
    height: 40px;
    color: white;
    display: flex;
    justify-content: center;

    gap: 100px;
    box-shadow: 4px 23px 16px 0px rgba(0,0,0,0.2);
    margin-left: -40px;
}

.faixa-header p {
    margin: 0px;
    line-height: 40px;
    font-size: .9rem;
}

@media (max-width: 500px){

    .faixa-header{
        display: none;
    }

    
}